.hero {
    padding: 224px 0px;
    z-index: 1;
    position: relative;
    overflow: hidden;
    background-color: hsl(var(--base));

    .hero--content {
        .title {
            font-size: 60px !important;
            text-transform: capitalize;
            line-height: 80px;
            margin-bottom: 42px;
        }

        .subtitle {
            margin-bottom: 48px;
        }
    }


    .hero-input--wrap {
        padding: 24px 16px;

        .select--wrap {
            select {
                background: transparent;
                border: none;
            }
        }

        .input--wrap {
            input {
                background: transparent;
                border: none;
                padding-left: 0px;
            }
        }

        .hero-serach--btn {
            top: 13px;
            right: 13px;
            padding: 10px;
        }
    }








    // bg circle

    .bg-circle {
        width: 475px;
        height: 475px;
        border-radius: 50%;
        filter: blur(173.5px);
    }

    .bg-circle--1 {
        position: absolute;
        top: 0px;
        left: 0px;
        background-color: hsl(var(--lemon-dark));
        opacity: 0.5;
    }

    .bg-circle--2 {
        position: absolute;
        left: 10%;
        bottom: -85px;
        background-color: hsl(var(--yellow-dark));
        opacity: 0.2;
    }

    .bg-circle--3 {
        position: absolute;
        top: -110px;
        left: 7%;
        background-color: hsl(var(--pink-dark));
        opacity: 0.3;
    }


    .bg-circle--4 {
        position: absolute;
        top: 0px;
        right: 15%;
        background-color: hsl(var(--lemon-dark));
        opacity: 0.3;
    }

    .bg-circle--5 {
        position: absolute;
        bottom: -136px;
        right: -20%;
        background-color: hsl(var(--yellow-dark));
        opacity: 0.2;
    }

    .bg-circle--6 {
        position: absolute;
        top: -226px;
        right: -360px;
        background-color: hsl(var(--pink-dark));
        opacity: 0.3;    
    }

    .bg-circle--7 {
        position: absolute;
        bottom: -80px;
        left: 38%;
        background-color: hsl(var(--cyan-light));
    }

    .bg-circle--8 {
        position: absolute;
        top: -80px;
        left: 38%;
        background-color: hsl(var(--cyan-light));
    }


}



// svg stroke animation
.svg-stroke--animation {
    stroke: hsl(var(--black)) !important;
    stroke-width: 0.5px !important;
    -webkit-animation: draw 7s ease infinite;
    animation: draw 7s ease infinite;
}

// category list
.hero-category--list {
    margin-bottom: 24px;
    .item {
        a {
            width: 36px;
            height: 36px;
        }
    }
}

@keyframes draw {
    0% {
        stroke-dasharray: 2000;
        stroke-dashoffset: 2000;
    }

    100% {
        stroke-dasharray: 2000;
        stroke-dashoffset: 0;
    }
}


.svg-fill--base {
    fill: hsl(var(--base));
}




// offer countdown box
.offer--coutdown {
   
    padding: 10px;


    .timer {
        padding: 6px 11px;
        border-radius: 4px;
        border: 1px solid rgba(255, 255, 255, 0.10);
        background: rgba(255, 255, 255, 0.13);
        backdrop-filter: blur(3.3499999046325684px);
    }

}