#loading {
	background-color: hsl(var(--base));
	height: 100%;
	width: 100%;
	position: fixed;
	z-index: 99999;
	margin-top: 0px;
	top: 0px;
}

#loading-center {
	width: 100%;
	height: 100%;
	position: relative;
}

#loading-center-absolute {
	position: absolute;
	left: 50%;
	top: 50%;
	height: 150px;
	width: 150px;
	margin-top: -75px;
	margin-left: -75px;
	display: flex;
	justify-content: center;
	align-items: center;
}

.loader {
	width: 64px;
	height: 64px;
	position: relative;
	background-image:
	  linear-gradient(hsl(var(--white)) 16px, transparent 0) ,
	  linear-gradient(hsl(var(--white)) 16px, transparent 0) ,
	  linear-gradient(hsl(var(--white)) 16px, transparent 0) ,
	  linear-gradient(hsl(var(--white)) 16px, transparent 0);
	background-repeat: no-repeat;
	background-size: 16px 16px;
	background-position: left top , left bottom , right top , right bottom;
	animation: rotate 1s linear infinite;
  }
  @keyframes rotate {
	0% {
	  width: 64px;
	  height: 64px;
	  transform: rotate(0deg)
	}
	50% {
	  width: 30px;
	  height: 30px;
	  transform: rotate(180deg)
	}
	100% {
	  width: 64px;
	  height: 64px;
	  transform: rotate(360deg)
	}
  }
